import React from 'react';
import './App.css';
import { Routes, Route, Navigate } from "react-router-dom";
import Header from './components/Header/Header';
import Navbar from './components/Navbar/Navbar';
import ProjectionContainer from './components/ProjectionMemory/ProjectionContainer';
import MemoriesContainer from './components/Memories/MemoriesContainer';
import FearsContainer from './components/Fears/FearsContainer';
import TransportContainer from './components/Transport/TransportContainer';
import DreamsContainer from './components/Dreams/DreamsContainer';
import SearchContainer from './components/Memories/Search/SearchContainer';
import SaveContainer from './components/Memories/Save/SaveContainer';
import Login from './components/Login/Login';
import RequireAuth from './components/hoc/withRequireAuth';
import SortContainer from './components/Memories/Sort/SortContainer';
import IslandsContainer from './components/Islands/IslandsContainer';
import ReactionContainer from './components/Reaction/ReactionContainer';


function App() {
  return (
    <div className="wrapper">
      <div className="generalContainer">
          <Header />
          <Navbar/>

          <div className="container">
            <Routes>
              <Route path="/InsideOut/" element={<Navigate replace to="/InsideOut/login" />} />
              <Route path='/InsideOut/projection' element={<ProjectionContainer />} />
              <Route path='/InsideOut/memories/search' element={<RequireAuth><SearchContainer /></RequireAuth>} />
              <Route path='/InsideOut/memories/sort' element={<SortContainer />} />
              <Route path='/InsideOut/memories/save' element={<RequireAuth><SaveContainer /></RequireAuth>} />
              <Route path='/InsideOut/memories' element={<RequireAuth><MemoriesContainer /></RequireAuth>} />
              <Route path='/InsideOut/transport' element={<RequireAuth><TransportContainer /></RequireAuth>} />
              <Route path='/InsideOut/dreams' element={<DreamsContainer />} />
              <Route path='/InsideOut/fears' element={<FearsContainer />} />
              <Route path='/InsideOut/islands' element={<RequireAuth><IslandsContainer /></RequireAuth>} />
              <Route path='/InsideOut/reaction' element={<ReactionContainer />} />
              <Route path='/InsideOut/login' element={<Login/>} />
            </Routes>
          </div>
      </div>
    </div>
  );
}

export default App;

